.Loader_White,
.Loader_White:after {
  border-radius: 50%;
  width: 10em;
  height: 10em;
}
.Loader_White {
  margin: 0 auto;
  font-size: 2px;
  position: relative;
  text-indent: -9999em;
  border-top: 1.1em solid rgba(230, 230, 230, 0.2);
  border-right: 1.1em solid rgba(230, 230, 230, 0.2);
  border-bottom: 1.1em solid rgba(230, 230, 230, 0.2);
  border-left: 1.1em solid #ffffff;
  -webkit-transform: translateZ(0);
  -ms-transform: translateZ(0);
  transform: translateZ(0);
  -webkit-animation: load8 1.1s infinite linear;
  animation: load8 1.1s infinite linear;
  overflow: hidden;
}

.Loader_Gray,
.Loader_Gray:after {
  border-radius: 50%;
  width: 10em;
  height: 10em;
}
.Loader_Gray {
  margin: 0 auto;
  font-size: 2px;
  position: relative;
  text-indent: -9999em;
  border-top: 1.1em solid rgba(134,134,134, 0.2);
  border-right: 1.1em solid rgba(134,134,134, 0.2);
  border-bottom: 1.1em solid rgba(134,134,134, 0.2);
  border-left: 1.1em solid #868686;
  -webkit-transform: translateZ(0);
  -ms-transform: translateZ(0);
  transform: translateZ(0);
  -webkit-animation: load8 1.1s infinite linear;
  animation: load8 1.1s infinite linear;
  overflow: hidden;
}

@-webkit-keyframes load8 {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
@keyframes load8 {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}


